import React, { useState } from 'react'
import ButtonSearch from '../atom/ButtonSearch'
import { Wrapper, FormStyles } from './Form.styles'

const province = [
  { name: 'dolnolskie', cities: ['Wrocaw', 'Legnica', 'Jelenia Gra'] },
  { name: 'kujawsko-pomorskie', cities: ['Bydgoszcz', 'Toru', 'Wocawek'] },
  { name: 'lubelskie', cities: ['Lublin', 'Zamo', 'Chem'] },
  {
    name: 'lubuskie',
    cities: ['Zielona Gra', 'Gorzw Wielkopolski', 'wiebodzin']
  },
  { name: 'dzkie', cities: ['d', 'Piotrkw Trybunalski', 'Skierniewice'] },
  { name: 'maopolskie', cities: ['Krakw', 'Tarnw', 'Nowy Scz'] },
  { name: 'mazowieckie', cities: ['Warszawa', 'Radom', 'Pock'] },
  { name: 'opolskie', cities: ['Opole', 'Kdzierzyn-Kole', 'Nysa'] },
  { name: 'podkarpackie', cities: ['Rzeszw', 'Przemyl', 'Tarnobrzeg'] },
  { name: 'podlaskie', cities: ['Biaystok', 'Suwaki', 'oma'] },
  { name: 'pomorskie', cities: ['Gdask', 'Gdynia', 'Supsk'] },
  { name: 'lskie', cities: ['Katowice', 'Bielsko-Biaa', 'Czstochowa'] },
  {
    name: 'witokrzyskie',
    cities: ['Kielce', 'Ostrowiec witokrzyski', 'Busko-Zdrj']
  },
  { name: 'warmisko-mazurskie', cities: ['Olsztyn', 'Elblg', 'Giycko'] },
  { name: 'wielkopolskie', cities: ['Pozna', 'Kalisz', 'Konin'] },
  {
    name: 'zachodniopomorskie',
    cities: ['Szczecin', 'Koszalin', 'winoujcie']
  }
]
const professions = [
  { id: 1, name: 'Architekt' },
  { id: 2, name: 'Inynier budownictwa' },
  { id: 3, name: 'Geodeta' },
  { id: 4, name: 'Technik budowlany' },
  { id: 5, name: 'Kierownik budowy' },
  { id: 6, name: 'Inspektor nadzoru budowlanego' },
  { id: 7, name: 'Elektryk budowlany' },
  { id: 8, name: 'Hydraulik budowlany' },
  { id: 9, name: 'Murarz' },
  { id: 10, name: 'Tynkarz' }
]

const Form = () => {
  const [selectedOption, setSelectedOption] = useState('')
  const [selectedOptionSecond, setSelectedOptionSecond] = useState('')
  const [selectedOptionThird, setSelectedOptionThird] = useState('')

  const handleSelectChange = e => {
    setSelectedOption(e.target.value.split(','))
  }
  const handleSelectChangeSecond = e => {
    setSelectedOptionSecond(e.target.value)
  }

  const handleSelectChangeThird = e => {
    setSelectedOptionThird(e.target.value)
  }
  return (
    <Wrapper>
      <FormStyles>
        <select value={selectedOption} onChange={handleSelectChange}>
          <option value=''>Wybierz Wojewdztwo...</option>
          {province.map(provinceData => (
            <option key={provinceData.name} value={provinceData.cities}>
              {provinceData.name}
            </option>
          ))}
        </select>
        {selectedOption && (
          <select
            value={selectedOptionSecond}
            onChange={handleSelectChangeSecond}
          >
            <option value=''>Wybierz Miasto...</option>
            {selectedOption.map(cities => (
              <option key={cities}>{cities}</option>
            ))}
          </select>
        )}
        <select value={selectedOptionThird} onChange={handleSelectChangeThird}>
          <option value=''>Wybierz Specjalizacj...</option>
          {professions.map(professionsName => (
            <option key={professionsName.id} value={professionsName.name}>
              {professionsName.name}
            </option>
          ))}
        </select>
        <ButtonSearch />
      </FormStyles>
      
    </Wrapper>
  )
}

export default Form
